import React, { useState, Fragment } from 'react';
import shortid from 'shortid';
import firebaseConf from './Firebase';
const Formu = () => {
    //Crear State de contactos
    const [contacto, actualizarContacto] = useState({
        email: '',
        nombre: '',
        fono: '',
        sms: ''
    });
     const [bAlerta, guardarbAlerta] = useState(false);
     const [tAlerta, guardartAlerta] = useState('');
     const [mAlerta, guardarmAlerta] = useState('');
   
    //Funcion que se ejecuta cada que el usuario escribe en un input
    const actualizarState = e => {
        actualizarContacto({
            ...contacto,
            [e.target.name]: e.target.value
        })
    }
    //Extraer los valores
    const { email, nombre, fono, sms } = contacto;
    //Cuando el usuario presiona agregar cita
    const submitContacto = e => { 
        e.preventDefault();
        //validar 
        if (email.trim() === '' || nombre.trim() === '' || fono.trim() === '' || sms.trim() === ''||sms.trim() === '') {
            
            guardarbAlerta(true);
            guardartAlerta('alert alert-danger');
            guardarmAlerta('Todos los campos son obligatorios');
            return ;  
        } else { 
            //Asignar un ID 
            contacto.id = shortid.generate();
            //Asignar la fecha
            var fecha = new Date();
            var dd = fecha.getDate();
            var mm = fecha.getMonth() + 1;
            var yyyy = fecha.getFullYear();
            fecha = dd + '/' + mm + '/' + yyyy;
            const contactos = {
                id: contacto.id,
                email: email,
                nombre: nombre,
                fono: fono,
                sms: sms,
                fecha: fecha
            };
            firebaseConf.database().ref(`contacts/${contactos.id}`).set(contactos).then(() => {   
                guardarbAlerta(true);
                guardartAlerta('alert alert-primary');
                guardarmAlerta('Mensaje enviado con Exicto');
            return ;   
        }).catch(() => {
            guardarbAlerta(true);
            guardartAlerta('alert alert-warning');
            guardarmAlerta('Error de la matrix : Mensaje no enviado');
                return ;   
            }
        );
        };
        actualizarContacto({
            email: '',
            nombre: '',
            fono: '',
            sms: ''
        });
    }
    return (
        <Fragment>
            { bAlerta ? <div className={tAlerta} role="alert">{mAlerta}</div> : null }
            <form onSubmit={submitContacto} className="ht hcontacto">
                <div className="form-group row">
                    <label className="col-sm-3 col-form-label">Email:</label>
                    <div className="col-sm-9">
                        <input name="email" value={email} type="email" className="form-control" placeholder="example@example.com" onChange={actualizarState} />
                    </div>
                </div>
                <div className="form-group row">
                    <label className="col-sm-3 col-form-label">Nombre:</label>
                    <div className="col-sm-9">
                        <input name="nombre" value={nombre} type="text" className="form-control"
                            placeholder="example" onChange={actualizarState} />
                    </div>
                </div>
                <div className="form-group row">
                    <label className="col-sm-3 col-form-label">Telefono:</label>
                    <div className="col-sm-9">
                        <input name="fono" value={fono} type="number" className="form-control" placeholder="999000999" onChange={actualizarState} />
                    </div>
                </div>
                <div className="form-group row">
                    <label className="col-sm-3 col-form-label">Mensaje:</label>
                    <div className="col-sm-9">
                        <textarea name="sms" value={sms} className="form-control" placeholder="Texto" rows="5" onChange={actualizarState} ></textarea>
                    </div>
                </div>
                <button type="submit" className=" btn btn-info border col">Enviar</button>
            </form>
        </Fragment>
    );

}

export default Formu;
